@font-face {
	font-family: "Lucida Sans Unicode";
	/* src: url("./fonts/Lucida\ Sans.woff2") format("woff2"); */
	src: url("../fonts/Lucida\ Sans.woff2") format("woff2");
}

* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}
a {
	text-decoration: none;
}

li {
	list-style: none;
}

html {
	font-size: 2rem;
	font-family: "Lucida Sans Unicode", sans-serif;
}

body {
	display: grid;
	place-content: center;
	min-height: 100vh;
	--primary-color: #009485;
	--secondary-color: #004b96;
	--secondary-color-light: #254a6f;
	--logo-text-fill: #ffffff;
}

main {
	max-width: 50ch;
	outline: none;
}
main:focus-visible {
	outline: none;
}
#root {
	display: grid;
	place-content: center;
	max-width: 90vw;
	/* overflow: hidden; */
}
