ul.related-words{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(max-content, 150px));
    column-gap: 15px;
    row-gap: 10px;
    margin: 1em 0;
    justify-content: space-evenly;
}
ul.related-words.spinner{
    grid-template-columns: unset;
    place-content: center;
}
ul.related-words li{
    font-size: 16px;
    cursor: pointer;
    transition: all 0.3s ease;
    color: rgba(0, 0, 0, .7);
}

/* button text hover effect */
ul.related-words li:hover{
    color: #00a8ff;
    /* color: var(--primary-color); */
    transform: scale(1.1);
    
}