/* SVG brand logos */
.brand {
	display: flex;
	align-items: center;
	justify-content: center;
	max-width: 400px;
	gap: 0.5rem;
	margin: auto;
}

#shapalokLogo {
	height: 2.5rem;
}
#letters path {
	/* fill: var(--logo-text-fill); */
	/* fill: var(--primary-color); */
	stroke: var(--primary-color);
	stroke-miterlimit: 10;
}

/* [END] SVG brand logos */

/* search form */
form {
	display: grid;
	place-content: center;
	align-items: center;
	/* position: relative; */
	--svg-width: 20px;
	--transition-duration: 200ms;
}

#searchBar {
	display: flex;
	align-items: center;
	margin-top: 0.5rem;
	width: 650px;
	border: 1px solid #dfe1e5;
	border-radius: 30px;
	padding: 0.25rem 0.5rem;
}

#searchBar.clicked {
	box-shadow: 0 1px 6px rgb(32 33 36 / 28%);
	border-color: rgba(223, 225, 229, 0);
}

#searchBar.open {
	box-shadow: 0 1px 6px rgb(32 33 36 / 28%);
	border-color: rgba(223, 225, 229, 0);
	border-bottom-left-radius: 0;
	border-bottom-right-radius: 0;
	border-bottom-color: transparent;
}

form svg {
	width: var(--svg-width);
	height: var(--svg-width);
}

#searchIcon {
	margin-right: 0.3rem;
}

#searchIcon span {
	display: flex;
	align-items: center;
}

#searchInput {
	display: grid;
	width: 100%;
}

#searchInput input {
	outline: none;
	border: none;
	font-size: 0.6rem;
	opacity: 0.8;
	width: 100%;
}

#searchInput button {
	display: none;
}

#searchButtons {
	display: flex;
	gap: 5px;
	justify-content: center;
	align-items: center;
}

#searchClearButton span {
	display: flex;
	align-items: center;
}

#searchClearButton {
	margin: 0 0 0 0.2rem;
	cursor: pointer;
	transition: 300ms cubic-bezier(0, -0.49, 0.45, 1.3);
	rotate: 360deg;
}

#searchClearButton svg {
	transition: var(--transition-duration) ease;
}

#searchClearButton:hover svg {
	transform: scale(1.1);
	fill: green;
}

#suggestionBox {
	/* display: block; */
	position: relative;
	margin-top: -1px;
}

ul#suggestionsList {
	position: absolute;
	border: 1px solid rgba(223, 225, 229, 0);
	border-radius: 30px;
	border-top-left-radius: 0;
	border-top-right-radius: 0;
	box-shadow: 0 4px 6px rgb(32 33 36 / 28%);
	border-top-color: transparent;
	background-color: white;
	width: 100%;
	overflow: hidden;
	padding-bottom: 0.2rem;
}

li.suggestion {
	cursor: pointer;
	display: flex;
	justify-content: space-between;
	padding: 0.1rem 0.5rem;
	font-size: 0.6rem;
	transition: var(--transition-duration) ease;
	border-bottom: 1px solid #f1f1f1;
}
li.suggestion:last-of-type{
	border-bottom: none;
}
.suggestionClickButton {
	display: grid;
	place-content: center;
}
li.suggestion:hover,
li.suggestion.active {
	background-color: #e2e2e2;
	font-weight: bold;
}

li.suggestion:hover .suggestionClickButton svg {
	opacity: 1;
	fill: green;
	transform: scale(1.1);
}

.suggestionKeyInput {
	display: none;
}

.suggestionClickButton svg {
	width: 16px;
	opacity: 0.5;
	transition: var(--transition-duration) ease;
}

/* SuggestionBox styles END */

@media only screen and (max-width: 500px) {
	.brand {
		max-width: 250px;
	}
	#shapalokLogo {
		height: 1.5rem;
	}
	form {
		max-width: 95vw;
	}
	#searchBar {
		max-width: 95vw;
		font-size: 0.7rem;
	}
	li.suggestion {
		padding-top: .5rem;
	}
	li.suggestion:first-of-type {
		padding-top: .1rem;
	}
}
