.ball {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%) rotate(0deg);
    border-radius: 50%;
    background-color: transparent;
    border: 2px solid transparent;
    animation: spinning 2s cubic-bezier(0.68, -0.75, 0.265, 1.75) infinite forwards;
}

.spinner_wrapper {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}


@keyframes spinning {
    100% {
        transform: translateX(-50%) translateY(-50%) rotate(360deg);
    }
}
